.sell-item {
  border-bottom: 1px solid #eeeeee;
  position: relative;
  padding: 0.16rem 0;
}
.sell-item .sell-item-l {
  width: 2.26rem;
}
.sell-item .sell-item-l h2 {
  color: #333333;
  font-size: 0.16rem;
  margin-bottom: 0.12rem;
}
.sell-item .sell-item-l p {
  line-height: 0.17rem;
  color: #999999;
  font-size: 0.12rem;
}
.sell-item .sell-item-l .time {
  margin-bottom: 0.12rem;
}
.sell-item .sell-item-r img {
  width: 1.09rem;
  height: 0.76rem;
  background: #ddd;
}
