.sell-list .sell-top {
  position: fixed;
  height: 0.5rem;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 10;
}
.sell-list .search-bar {
  background: #fff;
}
.sell-list .search-bar .am-search-input {
  background: #EDEDED;
}
.sell-detail .detail-banner {
  width: 3.75rem;
  height: 1.6rem;
}
.sell-detail .detail-banner img {
  width: 100%;
  height: 100%;
  background: gray;
}
.sell-detail .detail-tit {
  padding: 0.15rem;
  border-bottom: 1px solid #EEEEEE;
}
.sell-detail .detail-tit h2 {
  color: #333333;
  font-size: 0.18rem;
  margin-bottom: 0.05rem;
}
.sell-detail .detail-tit a {
  color: #e9b551;
  font-size: 0.14rem;
}
.sell-detail .detail-con .tit {
  padding: 0.16rem;
  width: 3.75rem;
  box-sizing: border-box;
}
.sell-detail .detail-con .tit h3 {
  left: 0.29rem;
  height: 0.2rem;
  line-height: 0.2rem;
  color: #333333;
  font-size: 0.18rem;
  margin-bottom: 0.13rem;
  text-indent: 0.13rem;
  border-left: 0.05rem solid #E9B551;
}
.sell-detail .detail-con .detail-i-con .detail-item {
  padding: 0.16rem;
  border-bottom: 1px solid #EEEEEE;
}
.sell-detail .detail-con .detail-i-con .detail-item .detail-item-l {
  width: 50%;
  text-align: left;
}
.sell-detail .detail-con .detail-i-con .detail-item .detail-item-l p {
  margin-bottom: 0.08rem;
}
.sell-detail .detail-con .detail-i-con .detail-item .detail-item-l .name {
  line-height: 0.22rem;
  color: #333333;
  font-size: 0.16rem;
}
.sell-detail .detail-con .detail-i-con .detail-item .detail-item-l .phone img {
  width: 0.12rem;
}
.sell-detail .detail-con .detail-i-con .detail-item .detail-item-l .phone a {
  line-height: 0.22rem;
  color: #e9b551;
  font-size: 0.14rem;
}
.sell-detail .detail-con .detail-i-con .detail-item .detail-item-l .company {
  line-height: 0.22rem;
  color: #AEAEAE;
  font-size: 0.14rem;
}
.sell-detail .detail-con .detail-i-con .detail-item .detail-item-r {
  width: 50%;
  text-align: right;
}
.sell-detail .detail-con .detail-i-con .detail-item .detail-item-r .tips {
  line-height: 0.22rem;
  color: #AEAEAE;
  font-size: 0.14rem;
  margin-bottom: 0.2rem;
}
.am-popover-arrow {
  background-color: rgba(0, 0, 0, 0.7);
}
